
import { defineComponent, reactive, Ref } from 'vue'
import Table from '@/components/table'
import Page from '@/components/admin/page'
import { AccountBookFilled } from "@ant-design/icons-vue";
import { Button, Card, Popconfirm, Input } from 'ant-design-vue';
import Container from '@/components/container';
import Br from '@/components/br';

export default defineComponent(() => {

    const data = reactive([
        { id: 33, name: '' },
        { id: 32, name: '' },
        { id: 31, name: '' }
    ])
    const columns = [
        {
            dataIndex: 'id',
            key: 'id',
            title: 'id'
        },
        {
            dataIndex: 'name',
            key: 'name',
            title: <text><AccountBookFilled /></text>,
            render: (data: any, index: number) => <div>{index}--{data.name}</div>
        },
        {
            dataIndex: 'operating',
            key: 'operating',
            title: <text></text>,
            render: (_data: any, index: number, list: Ref<any[]> ) => <div>
                <Button size='small' type='primary' style={{marginRight: '15px'}}></Button>
                <Popconfirm
                    title=''
                    okText=''
                    cancelText=''
                    onConfirm={() => {
                        if(list && list.value) {
                            list.value.splice(index, 1)
                        } else {
                            data.splice(index, 1)
                        }
                    }}
                    >
                    <Button size='small' type='danger' ></Button>
                </Popconfirm>
                
            </div>
        }
    ]

    const data2 = (search: any) => {
        console.log(search)
        return {
            data: [
                { id: 33, name: search.current+'' },
                { id: 33, name: search.current+'' },
                { id: 34, name: search.current+'awdawd' }
            ],
            total: 100
        };
    }

    return () => <Page title='table221'>
      
        <Card title=''>
            <Table data={data}
                bordered
                columns={columns}
                rowKey='id'>

            </Table>
        </Card>
        <Br />
        <Card title='render'>
            <Table data={data2}
                bordered
                columns={columns}
                rowKey='id'>

            </Table>
        </Card>
    </Page>

})